export default theme => ({
  root: {
    padding: '1.2rem 1.4rem',

    '& h4': {
      marginBottom: 0
    }
  },
  top: {
    display: 'flex',
    justifyContent: 'space-between',
    alignItems: 'flex-end',
    paddingBottom: 15,
    borderBottom: `1px solid rgba(0, 0, 0, 0.075)`
  },
  title: {
    '& span': {
      margin: '0 10px'
    }
  },
  price: {
    display: 'flex',
    alignItems: 'center',

    '& :first-child': {
      marginRight: 5,
    }
  },
  change: {
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'flex-end',

    '& h4': {
      color: theme.colors.green.main,
    },

    '& :last-child': {
      marginLeft: 10
    }
  }
})