export default theme => ({
  root: {
    backgroundColor: ['white', '!important'],
    boxShadow: theme.shadows.navbar
  },
  toggler: {
    '&:focus, &:active': {
      outline: ['none', '!important']
    }
  },
  button: {
    '@media(min-width: 992px)': {
      marginLeft: 25
    }
  },
  logo: {
    fontSize: '1rem',
    fontFamily: theme.fonts.family.alt
  }
})