import React, { Component } from 'react';
import { Container, Row, Col } from 'reactstrap';

import injectStyles from 'react-jss';
import styles from './index.style';
import chartOptions from './data';

import ReactEcharts from 'echarts-for-react';
import Section from 'components/section';
import Title from 'components/typography/title';
import Text from 'components/typography/text';
import Fade from 'react-reveal/Fade';


const content = {
  title: "Amounts traded",
  text: `Lorem ipsum dolor sit amet, consectetur adipisicing elit. 
  Rerum iste impedit labore eum, perferendis, maxime.`
}


class Trading extends Component {
  render() {
    let { classes, theme } = this.props;

    return (
      <Section className={classes.section}>
        <Container>
          <Fade delay={300} up cascade>
            <Row className="mb-4">
              <Col 
              className="text-center" 
              sm={{size: 8, offset: 2}} 
              md={{size: 6, offset: 3}}>
                <Title 
                color="white" 
                size="2.4rem">{content.title}</Title>
                <Text color="light">{content.text}</Text>
              </Col>
            </Row>

            <Row>
              <Col>
                <ReactEcharts 
                style={{height: '550px', width: '100%'}}
                option={chartOptions(theme)} />
              </Col>
            </Row>
          </Fade>
        </Container>
      </Section>
    );
  }
}

export default injectStyles(styles)(Trading)