import React, { Component } from 'react';
import data from './data';
import injectStyles from 'react-jss';
import styles from './index.style';
import { Container, Row, Col } from 'reactstrap';
import { Button } from 'reactstrap';

import Section from 'components/section';
import Card from 'components/card';
import Title from 'components/typography/title';
import Text from 'components/typography/text';
import Fade from 'react-reveal/Fade';

const content = {
  title: 'Features',
  text: `Lorem ipsum dolor sit amet, consectetur adipisicing elit. 
  Odit sapiente quisquam id iste, deleniti.`
}


class Features extends Component {
  state = {
    features: []
  }

  componentDidMount = () => {
    this.setState({ features: data })
  }

  renderFeatures = (items) => {
    return items.map((item, index) => {
      return <Col className="mb-4" md={6} lg={4} key={item.id}>
        <Fade up delay={(index > 2) ? 300 : 0}>
          <Card>
            <Title 
            noChange
            weight="medium" 
            className={this.props.classes.title}
            size="1rem">{item.title}</Title>
            <Text>{item.body}</Text>
          </Card>
          </Fade>
        </Col>
    })
  }

  render() {
    let { classes } = this.props;
    let { features } = this.state;

    return (
      <Section className={classes.section}>
        <Container>
          <Fade delay={300} up cascade>
            <Row className="mb-4">
              <Col sm={8} md={5}>
                <Title size="2.4rem">{content.title}</Title>
                <Text>{content.text}</Text>
              </Col>
            </Row>

            <Row>{this.renderFeatures(features)}</Row>

            <Row className="mt-4">
              <Col>
                <Button  
                color="primary"
                className={`shadow ${classes.button}`}>
                <span>Try now</span><i className="fa fa-long-arrow-right"></i>
                </Button>
              </Col>
            </Row>
          </Fade>
        </Container>
      </Section>
    );
  }
}

export default injectStyles(styles)(Features)