import React, { Component } from 'react';
import { Container, Row, Col } from 'reactstrap';
import { Button } from 'reactstrap';

import Title from 'components/typography/title';
import Text from 'components/typography/text';
import Quote from 'components/quote';

import injectStyles from 'react-jss';
import styles from './index.style';
import data from 'stocks';

const content = {
  text: `Lorem ipsum dolor sit amet, adipisicing elit earum quibusdam, 
  atque necessitatibus ab perspiciatis repellat neque eius maxime quos! 
  Consequuntur, quo, porro ab perspiciatis repell.`
}


class Header extends Component {
  renderStocks = () => {
    return data.map(item =>
      <Col className="mb-4" key={item.tick} md={6} lg={4}>
        <Quote input={item} />
      </Col>
    )
  }

  render() {
    let { classes } = this.props;

    return (
      <div className={classes.root}>
        <Container>
          <Row>
            <Col sm={12} md={10} lg={7} xl={6}>
              <div className={classes.labels}>
                <Text size="0.8rem" weight="medium" color="primary">Stocks</Text><span>/</span>
                <Text size="0.8rem" weight="medium" color="primary">Options</Text><span>/</span>
                <Text size="0.8rem" weight="medium" color="primary">Crypto</Text>
              </div>

              <Title className={classes.title} tag = "h1" size = "3rem">
                <span>AI powered,</span><br/>
                <span>trading platform.</span>
              </Title>

              <Text className={classes.text}>{content.text}</Text>

              <Button  
              color="primary"
              className={`shadow ${classes.button}`}>
              <span>Try now</span><i className="fa fa-long-arrow-right"></i>
              </Button>
            </Col>
          </Row>

          <Row className={classes.stocks}>
            {this.renderStocks()}
          </Row>
        </Container>
      </div>
    );
  }
}

export default injectStyles(styles)(Header)