const calcSize = (fontSize, mult, unit) => {
  const size = parseInt(fontSize)
  return `${size * mult}${unit}`
}

export default theme => ({
  rootTitle: {
    fontFamily: theme.fonts.family.alt,

    color: props => {
      if(props.color === "primary") {
        return theme.colors.primary.main
      } else if(props.color === 'light') {
        return theme.colors.text.light
      } else if(props.color === 'danger') {
        return theme.colors.red.main
      } else if(props.color === 'success') {
        return theme.colors.green.main
      } else if(props.color) {
        return props.color;
      } else {
        return theme.colors.text.body
      }
    },

    fontSize: ({ size }) => {
      return size && size;
    },

    fontWeight: ({ weight }) => {
      if(theme.fonts.weights.alt[weight]) {
        return theme.fonts.weights.alt[weight]
      }

      return theme.fonts.weights.alt.light
    },

    '@media(max-width: 992px)': {
      fontSize: ({ size, noChange }) => {
        if(noChange) return size;
        return size && calcSize(size, 0.85, 'rem');
      }
    },

    '@media(max-width: 480px)': {
      fontSize: ({ size, noChange }) => {
        if(noChange) return size;
        return size && calcSize(size, 0.75, 'rem');
      }
    }
  },

  rootText: {
    fontFamily: theme.fonts.family.main,
    fontWeight: ({ weight }) => {
      if(theme.fonts.weights.main[weight]) {
        return theme.fonts.weights.main[weight]
      }

      return theme.fonts.weights.main.normal
    },

    fontSize: ({ size }) => {
      return size && size;
    },

    color: props => {
      if(props.color === "primary") {
        return theme.colors.primary.main
      }

      if(props.color === 'light') {
        return theme.colors.text.light
      }

      if(props.color === 'danger') {
        return theme.colors.red.main
      }

      if(props.color === 'success') {
        return theme.colors.green.main
      }

      return theme.colors.text.body
    }
  }
})