export default theme => ({
  root: {
    display: 'flex',
    alignItems: 'center',
    minHeight: `calc(100vh - 54px)`,
    padding: {
      top: 120,
      bottom: 120
    },
    '@media(max-width: 992px)': {
      padding: {
        top: 90,
        bottom: 60
      }
    },

    '@media(max-width: 768px)': {
      padding: {
        top: 90,
        bottom: 40
      }
    }
  },
  text: {
    maxWidth: 450,
    marginTop: 20,
    marginBottom: 20
  },
  labels: {
    display: 'flex',
    alignItems: 'center',

    '& > span': {
      color: theme.colors.primary.main
    },

    '& > p': {
      marginRight: 10,
      marginBottom: 0,

      '&:not(:first-child)': {
        paddingLeft: 10
      }
    }
  },
  button: {
    '& i': {
      marginLeft: 15
    }
  },
  stocks: {
    marginTop: 110,

    '@media(max-width: 992px)': {
      marginTop: 90,
    },

    '@media(max-width: 768px)': {
      marginTop: 60,
    }
  }
})