export default theme => ({
  table: {
    width: '100%'
  },
  labels: {
    display: 'flex',
    alignItems: 'center',

    '& > span': {
      color: theme.colors.primary.main
    },

    '& > p': {
      marginRight: 10,
      marginBottom: 0,

      '&:not(:first-child)': {
        paddingLeft: 10
      }
    }
  },
  cell: {
    '& > img': {
      marginRight: 10
    },

    '& p': {
      marginBottom: 0
    }
  },
  header: {
    '& h4': {
      marginBottom: 0
    }
  },
  change: {
    display: 'flex',
    alignItems: 'center',

    '& > span': {
      lineHeight: 1.4,
      marginRight: 5,

      '&:first-child': {
        fontSize: 12
      }
    }
  },
  up: {
    color: theme.colors.green.main
  },
  down: {
    color: theme.colors.red.main
  }
})