import React from 'react';
import injectStyles from 'react-jss';
import styles from './index.style';
import classnames from 'classnames';


const Text = props => {
  const Tag = props.tag;

  const classes = classnames({
    [props.classes.rootText]: true,
    [props.className]: props.className
  })

  return <Tag 
  style={props.style} 
  className={classes}>{props.children}</Tag>
}


Text.defaultProps = {
  tag: 'p'
}


export default injectStyles(styles)(Text)