import React from 'react';
import injectStyles from 'react-jss';
import styles from './index.style';
import classnames from 'classnames';

import Card from 'components/card';
import Title from 'components/typography/title';
import options from './options';
import ReactEcharts from 'echarts-for-react';


const Quote = props => {
  const rootClass = classnames({
    [props.classes.root]: true,
    [props.className]: props.className
  })

  let {
    classes,
    input,
    theme
  } = props;

  const {
    tick = 'Tick',
      name = 'Name',
      price = "0,00",
      currency = '$',
      min = 100,
      change: {
        // type = 'up',
        points = '0',
        percent = '0%'
      } = {},
      data = null
  } = input;

  return <Card className={rootClass}>
    <div className={classes.top}>
      <div className={classes.title}>
        <Title 
        size="1rem" 
        weight="medium">{tick}</Title>
        <Title size="0.8rem">{name}</Title>
      </div>

      <div className={classes.right}>
        <div className={classes.change}>
          <Title weight="medium" size="0.9rem">{points}</Title>
          <Title size="0.85rem">({percent})</Title>
        </div>

       <div className={classes.price}>
          <Title size="0.95rem">{currency}</Title>
          <Title weight="medium">{price}</Title>
        </div>
      </div>
    </div>

    <div className={classes.bottom}>
      {
        data ? 
        <ReactEcharts 
        style={{height: 120, width: '100%'}}
        option={options({data, min, theme})} /> :
        <div>Not found</div>
      }
    </div>
  </Card>
}

Quote.defaultProps = {

}

export default injectStyles(styles)(Quote)