import React, { Component } from 'react';
import { Container, Row, Col } from 'reactstrap';

import Section from 'components/section';
import Timeline from 'components/timeline';
import Text from 'components/typography/text';
import Title from 'components/typography/title';
import Card from 'components/card';
import Fade from 'react-reveal/Fade';

import injectStyles from 'react-jss';
import styles from './index.style';
import data from './data';

const content = {
  title: 'Company history',
  text: `Lorem ipsum dolor sit amet, consectetur adipisicing elit. 
  Assumenda animi consequuntur dolor recusandae eius officiis?`
}


class MyTimeline extends Component {
  timelineItems = () => {
    let { classes } = this.props;

    return data.map((item, index) =>
      <Fade key={item.id} up>
        <Timeline.Item alternate index={index}>
          <Card className={classes.card}>
            <Text 
            className={classes.date} 
            size="0.75rem">{item.date}</Text>

            <Title 
            size="1rem" 
            weight="medium">{item.title}</Title>

            <Text 
            className={classes.text} 
            size="0.85rem">{item.text}</Text>
          </Card>
        </Timeline.Item>
      </Fade>
    )
  }

  renderTitle = () => {
    return <Fade delay={300} up>
      <Title size="2.4rem">{content.title}</Title>
      <Text>{content.text}</Text>
    </Fade>
  }

  render() {
    let { classes } = this.props;

    return (
      <Section className={classes.section}>
        <Container>
          <Row className="mb-4">
            <Col 
            className="text-center" 
            sm={{size: 8, offset: 2}} 
            md={{size: 6, offset: 3}}>
              {this.renderTitle()}
            </Col>
          </Row>

          <Row>
            <Col 
            lg={{size: 10, offset: 1}}
            xl={{size: 8, offset: 2}} >
              <Timeline alternate>
                {this.timelineItems()}
              </Timeline>
            </Col>
          </Row>
        </Container>
      </Section>
    );
  }
}

export default injectStyles(styles)(MyTimeline)