export default theme => ({
  timeline: {
    padding: 0,
    listStyleType: 'none'
  },
  timelineItem: {
    paddingBottom: 25,
    position: 'relative',
    display: 'flex'
  },
  tail: {
    top: 10,
    position: 'absolute',
    height: `calc(100% - 12px)`,
    width: 1,

    background: props => {
      let color = theme.colors.text.light;

      if(props.tail === 'primary') {
        color = theme.colors.primary.main
      } else if(props.tail === 'white') {
        color = 'white'
      } else if(props.tail) {
        color = props.tail
      }

      return color;
    },

    left: props => {
      return props.alternate ? '50%' : '0%'
    },

    '@media(max-width: 768px)': {
      left: '0% !important'
    }
  },
  head: {
    position: 'absolute',
    width: 8,
    height: 8,
    borderRadius: '50%',
    transform: 'translateX(-50%)',

    left: props => {
      return props.alternate ? '50%' : '0%'
    },

    '@media(max-width: 768px)': {
      left: '0% !important'
    },

    border: props => {
      let color = theme.colors.primary.main;

      if(props.color) {
        color = props.color;
      }

      return `1px solid ${color}`
    }
  },
  content: {
    top: '-10px',
    position: 'relative',
    padding: '0 15px',

    width: props => {
      return props.alternate ? '50%' : '100%'
    },

    '@media(max-width: 768px)': {
      width: '100% !important'
    }
  },
  right: {
    textAlign: 'left',

    left: props => {
      return props.alternate ? '50%' : '0%'
    },

    '@media(max-width: 768px)': {
      left: '0% !important'
    }
  }
})