const data = [
  { value: 335, name: 'Stocks' },
  { value: 310, name: 'Crypto' },
  { value: 274, name: 'Etf' },
  { value: 235, name: 'Options' },
  { value: 400, name: 'Forex' }
].sort((a, b) => a.value - b.value)

const legendData = data.map(({ name }) => name)


export default theme => ({
  baseOption: {
    title: {
      text: 'Trades for this year',
      subtext: 'in milions',
      left: 'center',
      top: 20,
      subtextStyle: {
        fontSize: 12,
        color: theme.colors.text.light,
        fontWeight: theme.fonts.weights.main.light,
        fontFamily: theme.fonts.family.alt
      },
      textStyle: {
        fontSize: 14,
        color: 'white',
        fontWeight: theme.fonts.weights.main.light,
        fontFamily: theme.fonts.family.alt
      }
    },
    tooltip: {
      trigger: 'item',
      formatter: "{a} <br/>{b} : {c} ({d}%)"
    },
    legend: {
      data: legendData,
      type: 'scroll',
      orient: 'vertical',
      show: true,
      top: 'center',
      right: '10%',
      padding: 20,
      borderRadius: 5,
      backgroundColor: 'rgba(133, 132, 198, 0.075)',
      shadowColor: 'rgba(0, 0, 0, 0.35)',
      shadowOffsetY: 3,
      shadowBlur: 10,
      textStyle: {
        color: theme.colors.text.light,
        fontWeight: theme.fonts.weights.main.light,
        fontFamily: theme.fonts.family.alt
      },
    },
    visualMap: {
      show: false,
      min: 80,
      max: 600,
      inRange: {
        colorLightness: [0, 1]
      }
    },

    series: [{
      name: 'Amount traded',
      type: 'pie',
      radius: '65%',
      center: ['50%', '50%'],
      data: data,
      roseType: 'radius',
      label: {
        formatter: [
          '{b|{b}}',
          '{c|{c} ({d}%)}'
        ].join('\n'),
        rich: {
          b: {
            color: 'white',
            fontSize: 16,
            lineHeight: 18,
            fontFamily: theme.fonts.family.alt,
            fontWeight: theme.fonts.weights.main.light
          },
          c: {
            fontSize: 12,
            lineHeight: 18,
            color: theme.colors.text.light,
            fontFamily: theme.fonts.family.alt,
            fontWeight: theme.fonts.weights.main.light
          }
        }
      },
      labelLine: {
        normal: {
          lineStyle: {
            color: 'rgba(255, 255, 255, 0.3)'
          },
          smooth: 0.2,
          length: 10,
          length2: 20
        }
      },
      itemStyle: {
        normal: {
          color: '#4a4998',
        },
        emphasis: {
          shadowBlur: 10,
          shadowOffsetX: 0,
          shadowColor: 'rgba(0, 0, 0, 0.4)'
        }
      },

      animationType: 'scale',
      animationEasing: 'elasticOut',
      animationDelay: function(idx) {
        return Math.random() * 200;
      }
    }]
  },
  media: [{
      query: {
        minWidth: 768,
        maxWidth: 992
      },
      option: {
        legend: {
          orient: 'vertical',
          top: 'center',
          right: '0%'
        },
      }
    },
    {
      query: {
        maxWidth: 768
      },
      option: {
        legend: {
          orient: 'horizontal',
          bottom: 0,
          right: 'center'
        },
      }
    }
  ],
})