import React from 'react';
import Text from 'components/typography/text';
import Title from 'components/typography/title';


const Cell = ({ classes, value, children }) => (
  children ?
  <div className={classes.cell}>
    {children}
  </div> :
  <div className={classes.cell}>
    <Text size="1rem">{value}</Text>
  </div>
)


const Header = (props) => (
  <div className={props.classes.header}>
    <Title 
    size="0.85rem" 
    weight="medium">{props.name}</Title>
  </div>
)


export default ({
  classes
}) => {
  const base = 'https://www.cryptocompare.com'

  return [{
    accessor: 'CoinInfo.Name',
    Header: props => <Header name="Name" classes={classes} />,
    Cell: props => {
      const img = props.original.CoinInfo.ImageUrl;

      return <div className={classes.cell}>
        <img width="15px" src={`${base}${img}`} alt=""/>
        <span>{props.value}</span>
      </div>
    }
  }, {
    accessor: 'DISPLAY.USD.PRICE',
    Header: props => <Header name="Price" classes={classes} />,
    Cell: props => <Cell {...props} classes={classes} />
  }, {
    accessor: 'DISPLAY.USD.MKTCAP',
    Header: props => <Header name="Market Cap" classes={classes} />,
    Cell: props => <Cell {...props} classes={classes} />
  }, {
    accessor: 'DISPLAY.USD.VOLUME24HOUR',
    Header: props => <Header name="Volume 24h" classes={classes} />,
    Cell: props => <Cell {...props} classes={classes} />
  }, {
    accessor: 'DISPLAY.USD.SUPPLY',
    Header: props => <Header name="Supply" classes={classes} />,
    Cell: props => <Cell {...props} classes={classes} />
  }, {
    accessor: 'DISPLAY.USD.CHANGEPCT24HOUR',
    Header: props => <Header name="Change 24h" classes={classes} />,
    width: 120,
    Cell: props =>
      <Cell {...props} classes={classes}>
      {
        props.value[0] === '-' ?
        <Text
        className={`${classes.down} ${classes.change}`} 
        size="1rem">
          <span><i className="fa fa-long-arrow-down" aria-hidden="true"></i></span> 
          <span>{props.value}</span>
        </Text> :
        <Text
        className={`${classes.up} ${classes.change}`} 
        size="1rem">
          <span><i className="fa fa-long-arrow-up" aria-hidden="true"></i></span> 
          <span>{props.value}</span>
        </Text>
      }
    </Cell>
  }]
}