import React, { Component } from 'react';
import injectStyles from 'react-jss';
import styles from './index.style';
import classnames from 'classnames';


const {
  Provider,
  Consumer
} = React.createContext();


class Timeline extends Component {
  render() {
    let {
      classes,
      children
    } = this.props;

    return (
      <ul className={classes.timeline}>
        <Provider value={this.props}>
          {children}
        </Provider>
      </ul>
    )
  }
}


Timeline.Item = ({ children, index, classes }) => {
  return <Consumer>
    {(value) => {
      const {alternate} = value;
      const right = index % 2 === 0;

      const contentStyle = classnames({
        [classes.content]: true,
        [classes.right]: alternate && right
      })

      return (
        <li className={classes.timelineItem}>
          <div className={classes.head}></div>
          <div className={classes.tail}></div>
          <div className={contentStyle}>{children}</div>
        </li>
      )
    }}
  </Consumer>
}


Timeline.Item = injectStyles(styles)(Timeline.Item)
export default injectStyles(styles)(Timeline)