import React, { Component } from 'react';
import { Container, Row, Col } from 'reactstrap';
import { Input, InputGroup, InputGroupAddon } from 'reactstrap';
import { Nav, NavItem, Button } from 'reactstrap';
import { Link } from 'react-router-dom';

import Title from 'components/typography/title';
import Text from 'components/typography/text';
import Section from 'components/section';

import injectStyles from 'react-jss';
import styles from './index.style';


class Footer extends Component {
  info = () => {
    let { classes } = this.props;

    return (
      <Col className="mb-4" sm={4}>
        <Title weight="bold">Crypto</Title>
        <Text>Lorem ipsum dolor sit amet, consectetur adipisicing elit. Saepe, architecto.</Text>

        <Text className={classes.info}>
          <i className="fa fa-envelope-o" aria-hidden="true"></i>
          <span>support@crypto.com</span>
        </Text>

        <Text className={classes.info}>
          <i className="fa fa-phone" aria-hidden="true"></i>
          <span>+ (123) 444 - 5555</span>
        </Text>
      </Col>
    )
  }

  subscribe = () => {
    let { classes } = this.props;

    return (
      <Col className="mb-4" sm={5}>
        <Title size="1rem" weight="bold">Lets keep in touch</Title>

        <InputGroup className="mt-4">
          <Input className={classes.input} placeholder="Your email..." />
          <InputGroupAddon addonType="append">
            <Button color="primary">Send</Button>
          </InputGroupAddon>
        </InputGroup>
      </Col>
    )
  }

  nav = () => {
    return (
      <Col className="mb-4" sm={3}>
        <Title size="1rem" weight="bold">Company</Title>

        <Nav vertical>
          <NavItem>
            <Link to="/">
              <Text 
              size="0.9rem" 
              weight="normal">Home</Text>
            </Link>
          </NavItem>
          <NavItem>
            <Link to="/">
              <Text 
              size="0.9rem" 
              weight="normal">Faq</Text>
            </Link>
          </NavItem>
          <NavItem>
            <Link to="/">
              <Text 
              size="0.9rem" 
              weight="normal">Try demo</Text>
            </Link>
          </NavItem>
        </Nav>
      </Col>
    )
  }

  render() {
    let { classes } = this.props;

    return (
      <Section className={classes.root}>
        <Container>
          <Row>
            {this.info()}
            {this.nav()}
            {this.subscribe()}
          </Row>
        </Container>
      </Section>
    );
  }
}

export default injectStyles(styles)(Footer)