import React, { Component } from 'react';
import {Switch, Route} from 'react-router-dom';
import injectStyles from 'react-jss';
import styles from 'assets/jss/global';

import Navbar from 'components/navbar';
import Home from 'pages/home';

const PageOne = () => <div>Page 1</div>
const PageTwo = () => <div>Page 2</div>


class App extends Component {
  render() {
    return (
      <div className="App">
        <Navbar />

        <Switch>
          <Route exact path="/" component={Home} />
          <Route exact path="/page_1" component={PageOne} />
          <Route exact path="/page_2" component={PageTwo} />
        </Switch>
      </div>
    );
  }
}

export default injectStyles(styles)(App)
