import echarts from 'echarts'
import Color from 'color';

export default ({ data, min, theme }) => ({
  grid: {
    left: 0,
    top: 20,
    right: 0,
    bottom: 20
  },
  tooltip: {
    trigger: 'axis',
    formatter: function(params) {
      params = params[0];
      var date = new Date(params.name);
      return date.getDate() +
        '/' + (date.getMonth() + 1) +
        '/' + date.getFullYear() +
        ' : ' + params.value[1];
    },
    textStyle: {
      fontSize: 12
    },
    axisPointer: {
      type: 'cross',
      label: {
        show: false
      }
    }
  },
  xAxis: {
    type: 'time',

    splitLine: {
      show: false
    }
  },
  yAxis: {
    show: false,
    min
  },
  series: [{
    name: 'Stock data',
    type: 'line',
    smooth: true,
    symbol: 'none',
    sampling: 'average',
    itemStyle: {
      color: Color(theme.colors.primary.main).alpha(0.7).toString()
    },
    areaStyle: {
      color: new echarts.graphic.LinearGradient(0, 0, 0, 1, [{
        offset: 0,
        color: 'rgb(96, 72, 255)'
      }, {
        offset: 1,
        color: 'rgb(139, 138, 212)'
      }])
    },
    data
  }]
})