import React, { Component } from 'react';
import data from './data';
import injectStyles from 'react-jss';
import ReactTable from "react-table";
import { Container, Row, Col } from 'reactstrap';
import { Button } from 'reactstrap';

import Section from 'components/section';
import Title from 'components/typography/title';
import Text from 'components/typography/text';
import Fade from 'react-reveal/Fade';

import styles from './index.style';
import columns from './columns';


const content = {
  title: 'Live cryptocurrency data',
  text: `Lorem ipsum dolor sit amet, consectetur adipisicing elit. 
  Reprehenderit, sit. Ullam voluptatum alias, dolorem voluptates!`
}


class Crypto extends Component {
  renderTitle = () => {
    let { classes } = this.props;

    return (
      <Col sm={8} md={5}>
        <div className={classes.labels}>
          <Text size="0.8rem" weight="medium" color="primary">Live</Text><span>/</span>
          <Text size="0.8rem" weight="medium" color="primary">Free API</Text><span>/</span>
          <Text size="0.8rem" weight="medium" color="primary">24h Support</Text>
        </div>

        <Title size="2.4rem">{content.title}</Title>
        <Text>{content.text}</Text>
      </Col>
    )
  }

  renderTable = () => {
    const { classes } = this.props;

    const Nodata = (
      <div className="rt-noData">
        <div >No users found</div>
      </div>
    )

    return <ReactTable 
      minRows={10}
      defaultPageSize={15}
      className={classes.table}
      showPagination={false}
      NoDataComponent={() => <Nodata />}
      columns={columns({classes})}
      data={data} />
  }

  tryNow = () => {
    const { classes } = this.props;

    return (
      <Row className="mt-4">
        <Col>
          <Button  
          color="primary"
          className={`shadow ${classes.button}`}>
          <span>Try now</span><i className="fa fa-long-arrow-right"></i>
          </Button>
        </Col>
      </Row>
    )
  }

  render() {
    let { classes } = this.props;

    return (
      <Section className={classes.section}>
        <Container>
          <Fade delay={300} up cascade>
            <Row className="mb-4">{this.renderTitle()} </Row>
            <Row className="mb-4">{this.renderTable()}</Row>
          </Fade>
        </Container>
      </Section>
    );
  }
}

export default injectStyles(styles)(Crypto)